.review-block {
    .sertify-slider, .review-video-slider, .reviews-slider  {
        .slick-dots {
            bottom: -50px;
            left: 0;
        }
        .slick-dots li button:before {
            font-size: 11px;
            content: '';
            width: 8px;
            height: 8px;
            border-radius: 50%;
            border: 2px solid #f3ecde;
            background-color: transparent;
            opacity: 1;
        }
        .slick-dots li.slick-active button:before {
            background-color: #f3ecde;
            opacity: 1;
        }
        .slick-dots li button:hover:before,
        .slick-dots li button:focus:before {
            background-color: #f3ecde;
        }
    }
    .sertify-slider {
        padding: 0 160px;
        margin: 50px auto 100px auto;
        .sertify-slider__item {
            text-align: center;
            div {
                width: 160px;
                height: 210px;
                border: solid 6px #faf4e9;
                margin: 0 auto 26px auto;
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
            img {
                width: 100%;
                height: 100%;
            }
        }
    }

    .review-video-slider {
        margin-top: 40px;
        margin-bottom: 87px;
        padding: 0 30px;
        .review-video-slider__item {
            width: 280px;
            p {
                text-align: center;
                margin-top: 3px;
                letter-spacing: 0.5px;
                padding-right: 14px;
            }
            iframe {
                width: 280px;
                margin: 0 auto;
            }
        }
        .slick-arrow {
            top: 92px;
        }
    }
    .reviews-slider {
        margin-top: 60px;
        padding: 0 30px;
        .reviews-slider__item {
            text-align: center;
            width: 288px;
            img {
                width: 120px;
                height: 120px;
                border-radius: 50%;
                margin: 0 auto;
                margin-bottom: 20px;
            }
            h3 {
                font-size: 12px;
                font-weight: bold;
                text-align: center;
                color: #b9a98d;
                width: 100%;
                margin-bottom: 28px;
            }
            p {
                font-size: 15px;
                line-height: 26px;
                text-align: center;
                color: #000000;
                margin-bottom: 20px;
            }
            a {
                font-size: 14px;
                text-align: center;
                color: #026da1;
            }
        }
        .reviews-slider__content {
            width: 288px;
            margin: 0 auto;
        }
    }
}


